import * as React from 'react';
import styles from './Tour.module.scss';
import introJs from 'intro.js';
import 'intro.js/introjs.css';
import { CompoundButton } from 'office-ui-fabric-react';
import { TourHelper } from './TourHelper';
import { ITourProps } from './ITourProps';

export interface ITourState {
  steps: Array<{ selector: string; content: string }>;
  tourDisabled: boolean;
}

export default class Tour extends React.Component<ITourProps, ITourState> {
  constructor(props: ITourProps) {
    super(props);
    this.state = { steps: [], tourDisabled: true };
  }

  public componentDidMount(): void {
    this.initializeTourWithRetry();
  }

  public componentDidUpdate(prevProps: ITourProps): void {
    if (JSON.stringify(this.props.collectionData) !== JSON.stringify(prevProps.collectionData)) {
      this.initializeTourWithRetry();
    }
  }

  private initializeTourWithRetry(attempt = 0): void {
    const MAX_ATTEMPTS = 5;
    const DELAY_MS = 500;
    const sortedSettings = [...this.props.collectionData].sort((a, b) => {
      const pa = Number(a.Position) || 0;
      const pb = Number(b.Position) || 0;
      return pa - pb;
    });
    const rawSteps = TourHelper.getTourSteps(sortedSettings);

    if (rawSteps.length > 0 || attempt >= MAX_ATTEMPTS) {
      this.setState({
        steps: rawSteps.map(s => ({ selector: s.selector, content: s.content })),
        tourDisabled: rawSteps.length === 0
      });
    } else {
      setTimeout(() => this.initializeTourWithRetry(attempt + 1), DELAY_MS);
    }
  }

  /** Safely resolves a selector to an Element or null */
  private getElement(selector: string): HTMLElement | null {
    try {
      return document.querySelector(selector) as HTMLElement;
    } catch {
      return null;
    }
  }

  private smoothScrollElement(element, options = {}) {
    return new Promise((resolve, reject) => { // Added reject for potential errors
      // Get the scrollable container element
      const scrollContainer = document.querySelector<HTMLElement>(
        `[data-automation-id="${this.props.dataAutomationId}"]`
      );

      if (!scrollContainer) {
        // If the container element is not found, reject the promise
        reject(new Error('Scroll container not found.'));
        return; // Exit the function
      }

      // Initiate the scroll using scrollIntoView()
      // It's still generally good practice to use scrollIntoView() on the target element,
      // as it handles the logic of bringing that specific element into view
      // within its scrollable ancestor.
      element.scrollIntoView({ behavior: 'smooth', ...options });

      // Listen for the 'scrollend' event on the specific scroll container
      const onScrollEnd = () => {
        // Clean up the event listener
        scrollContainer.removeEventListener('scrollend', onScrollEnd);
        // Resolve the promise with true
        resolve(true);
      };

      // Add the event listener to the specific scroll container
      scrollContainer.addEventListener('scrollend', onScrollEnd);

      // Optional: Add a timeout to reject the promise if scrollend doesn't fire
      const timeout = setTimeout(() => {
        scrollContainer.removeEventListener('scrollend', onScrollEnd);
        reject(new Error('Scroll timed out.'));
      }, 1000); // Adjust timeout duration as needed
    });
  }


  /**
   * Open the tour using Intro.js; relies on disableDeferLoading in manifest to have all content loaded
   */
  private _openTour = async (): Promise<void> => {
    let prescrollEl = document.getElementById('117a4572-4c6e-4d24-a4fd-94488a744547');
    if (prescrollEl) {
      try {
        await this.smoothScrollElement(prescrollEl, { block: 'center' });
      } catch (error) {
        console.error('Error during pre-scroll:', error);
        return;
      }
    }

    const { steps } = this.state;
    if (steps.length === 0) {
      return;
    }

    // 1) Build the Intro.js steps as before
    const introSteps = steps.map(step => {
      const container = this.getElement(step.selector);
      let target: HTMLElement | string = step.selector;
      if (container) {
        const inner = container.querySelector(
          '.ms-webpart-titleText, .ms-webpart-body'
        ) as HTMLElement;
        target = inner || container;
      }
      return {
        element: target,
        intro: step.content
      };
    });

    // 2) Create the tour instance
    const intro = introJs().setOptions({ steps: introSteps, scrollToElement: false });

    // 3) Hook into each step change to scroll it into view
    intro.onbeforechange((targetEl: HTMLElement) => {
      // Try to scroll within the SPFx container first
      const scrollContainer = document.querySelector<HTMLElement>(
        `[data-automation-id="${this.props.dataAutomationId}"]`
      ); //mainScrollRegionInnerContent

              // Center the element vertically in that container
        const top =
          targetEl.offsetTop -
          scrollContainer.clientHeight / 2 +
          targetEl.clientHeight / 2;
        scrollContainer.scrollTop = top;
        targetEl.scrollIntoView({ block: 'center', behavior: 'smooth' });

/*       if (scrollContainer) {
        // Center the element vertically in that container
        const top =
          targetEl.offsetTop -
          scrollContainer.clientHeight / 2 +
          targetEl.clientHeight / 2;
        scrollContainer.scrollTop = top;
        targetEl.scrollIntoView({ block: 'center', behavior: 'smooth' });
      } else {
        // Fallback to window scroll
        targetEl.scrollIntoView({ block: 'center', behavior: 'smooth' });
      } */

      // Recompute the overlay/spotlight
      //setTimeout(() => intro.refresh(), this.props.preloadTimeout);

      return true;
    });

    intro.onafterchange((targetEl: HTMLElement) => {
      const canvas = document.querySelector<HTMLElement>('.CanvasComponent');
      if (canvas) {
        canvas.classList.add('forceTableReflow', 'forceVerticalSectionReflow');
        void canvas.offsetHeight;
        canvas.classList.remove('forceTableReflow', 'forceVerticalSectionReflow');
      }
      
      // Recompute the overlay/spotlight
      setTimeout(() => intro.refresh(), this.props.preloadTimeout);
    });

    // 4) Finally, start the tour one time
    intro.start();
};

  private _closeTour = (): void => {
    introJs().exit(true);
  };

  public render(): React.ReactElement {
    return (
      <div className={styles.tour}>
        <CompoundButton
          primary
          text={this.props.actionValue}
          secondaryText={this.props.description}
          disabled={this.state.tourDisabled}
          onClick={this._openTour}
          className={styles.tutorialButton}
        />
      </div>
    );
  }
}